}

.screen-sub-heading {
  font-weight: 500;
  letter-spacing: 3px;
  margin: 8px 0 18px 0;
  font-size: 1rem;
  color: black;
}

.screen-heading {
  font-size: 2.5rem;
  font-weight: 700;
}

.heading-separator {
  display: flex;
