  position: relative;
  width: 180px;
  margin: 10px 0 0 0;
}

.separator-line {
  width: 100%;
  height: 2px;
  background-color: #1f2235;
}

.separator-blob {
  height: 10px;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
}

.separator-blob div {
  width: 50px;
  position: relative;
