  color: white;
}

.header-logo {
  letter-spacing: 4px;
  font-family: 'Montserrat';
  font-weight: 500;
  color: white;
}

.header-logo span {
  color: red;
}

.header-options {
  display: flex;
  align-items: center;
}

.header-option {
  font-size: 20px;
  cursor: pointer;
  font-weight: 600;
}

.header-hamburger {
  display: none;
  color: white;
}

.header-option-separator {
  margin: 0 50px 0 0;
  color: white;
}

.selected-header-option {
  color: rebeccapurple;
}

@media only screen and (max-width: 1110px) {
  .header-parent {
    width: 80%;
  }
}

@media only screen and (max-width: 970px) {
  .header-hamburger {
    display: block;
  }
  .header-parent {
    width: 100%;
    padding: 0 40px;
    position: relative;
  }
  .header-options {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: -100%;
    top: 0px;
    height: 100vh;
    width: 100%;
    justify-content: space-around;
    background-color: #1f2235;
    z-index: 1000;
    opacity: 0.8;
    transition: all 0.8s ease-out;
  }
  .header-option-separator {
    margin: 0;
  }
  .selected-header-option {
    color: var(--white);
  }
  .header-option {
    color: var(--white);
  }
  .header-hamburger-bars {
    font-size: 30px;
    cursor: pointer;
    margin: 0px 10px;
    color: var(--white);
  }
  .show-hamburger-options {
    left: 0;
    opacity: 1;
  }
}

.go-home-btn {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: solid 1px black;
  background-color: white;
  position: fixed;
  right: 25px;
  bottom: 25px;
}

.go-home-btn:hover {
  border: solid 1px grey;
}

